/* Background for the transparensii-container */
.transparensii-container {
  min-height: 100vh;
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  object-fit: contain;
}

video {
  position: fixed;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

/* Styling for the content section */
.content {
  display: flex;
  flex-direction: column;
  max-width: 800px;
  color: #fff;
  padding: 20px;
  margin-top: -100px;
  text-align: center;
}

.content h1 {
  flex: 1;
  font-size: 5rem;
  margin-top: 3rem;
  margin-left: 0; /* Reset margin */
}

.content p {
  flex: 1;
  font-size: 1.5rem;
  margin-top: 20px;
  margin-left: 0; /* Reset margin */
}

.content p:not(:last-child) {
  margin-bottom: 20px;
}

.presentation-container {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  animation-name: slideInUp;
  animation-duration: 1s;
  animation-timing-function: ease-in;
  animation-delay: 0.1s;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: none;
}

.card__container {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
}

.card2 {
  min-width: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;

  max-width: 220px;
  max-height: 300px;
  padding: 35px;
  margin: 10px;

  border: 1px solid rgba(255, 255, 255, .25);
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.45);
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);

  backdrop-filter: blur(15px);
}

.card2 p {
font-size: 18px;
color: #fff;
margin-bottom: 100%;
}

.icone {
  text-align: center;
  justify-content: center;
  font-size: 3rem;
}

.rotate-card {
  transform: rotate(360deg);
}

@keyframes slideInLeft {
  from {
    transform: translateX(-300px);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes slideInUp {
  from {
    transform: translateY(300px);
  }
  to {
    transform: translateY(0);
  }
}

@media screen and (max-width: 1500px) {
  .card2 {
    max-width: 170px;
    max-height: 200px;
    padding: 25px;
    margin: 5px;
  }

  .card2 p {
    font-size: 14px;
    color: #fff;
    }
  
    .icone {
      text-align: center;
      justify-content: center;
      font-size: 2rem;
    }
  }

/* Media Query for Smaller Screens */
@media screen and (max-width: 768px) {
  .transparensii-container {
    align-items: center;
    justify-content: center;
  }

  .content {
    margin-top: 0; /* Reset margin */
    padding: 10px; /* Adjust padding for smaller screens */
  }

  .content h1 {
    font-size: 2.5rem;
    
  }

  .content p {
    font-size: 1.5rem;
  }
  .card2 {
    max-width: 150px;
    max-height: 180px;
    padding: 25px;
    margin: 5px;
  }

  .card2 p {
    font-size: 12px;
    color: #fff;
    }
  
    .icone {
      text-align: center;
      justify-content: center;
      font-size: 2rem;
    }
}

@media screen and (max-width: 480px) {
  .video-container video {
    display: none;
    visibility: collapse;
  }
    .media-player {
      display: none;
    }

    .content video {
      width: 50%; /* Set the width to an appropriate value */
      height: auto;;
    }
  
  .transparensii-container {
    background-image: url("/public/Photo-site/Mobile_projet.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }

  .content p {
    font-size: 1rem;
  }

  .card2 {
    max-width: 110px;
    max-height: 150px;
    padding: 25px;
    margin: 5px;
  }

  .card2 p {
    font-size: 8px;
    color: #fff;
    }
  
    .icone {
      text-align: center;
      justify-content: center;
      font-size: 1.5rem;
    }
}